import React, { Component } from 'react';
import axios from 'axios';
import Movie from './Movie';
import './App.css';

class App extends Component {
  state = {
    isLoading: true,
    movies: [],
  }

  getMoive = async () => {
    const { data: { data: { movies } }} = await axios.get("https://yts-proxy.now.sh/list_movies.json?sort_by=rating");
    this.setState({ movies, isLoading: false })
    // axios ê°€ ë¹„ë™ê¸°ë¼ì„œ í”„ë¡œë¯¸ìŠ¤ì²˜ë¦¬ë¥¼ í•´ì£¼ì–´ì•¼í•¨
  }

  componentDidMount() {
    this.getMoive()
  }


  render() {
    const { isLoading, movies } = this.state;
    return(
      <section className='container'>
      {isLoading ? (
        <div className='loader'>
          <span className='loader__text'>Loading...</span>
        </div> ) : (
          <div className='movies'>
            {movies.map(movie => <Movie genres={movie.genres} key={movie.id} id={movie.id} title={movie.title} year={movie.year} summary={movie.summary} poster={movie.medium_cover_image} />)}
          </div>
        )
      }
    </section> 
    )
  }
};

export default App;
