import React, {useState} from 'react';
import PropTypes from 'prop-types';
import './Movie.css';

const Movie = ({ title, summary, poster, year, genres }) => {
    const [more, setMore] = useState(false)

    const clickMore = () => {
        if(more) {
            setMore(false)
        } else {
            setMore(true)
        }
    };


    return (
    <div className='movie'>
        <img src={poster} alt={title} title={title} />
        <div className='movie__data'>
            <h3 className='movie__title'>{title}</h3>
            <h5 className='movie__year'>{year}</h5>
            <ul className='movie__genres'>{genres.map((genre, i) => <li key={i} className='genres__genre' >{genre}</li> )}</ul>
            <p className='movie__summary'>{more ? summary :summary.slice(0, 180)}{summary.length > 180 ? <span onClick={clickMore}>{more ? 'ğŸ”ºì ‘ê¸°' :'...'}</span> : ''}</p>
        </div>
    </div>
    )
}

Movie.propTypes = {
    id: PropTypes.number.isRequired,
    title: PropTypes.string.isRequired,
    summary: PropTypes.string.isRequired,
    poster: PropTypes.string.isRequired,
    year: PropTypes.number.isRequired,
}

export default Movie;

